import React from 'react';
import {Form} from 'react-final-form'
import {Box, Button} from '@mui/material';
import axios from "axios";
import Cookies from 'universal-cookie';
import { TextField } from 'mui-rff';

export const Login = ({setLoggedIn}) => {
    const handleSubmit = async (data) => axios.post('http://34.194.225.209:8080/authentication/login', data)
        .then(function (response) {
            const cookies = new Cookies();

            cookies.set('token', response.data.jwt_token);
            setLoggedIn(true);
        });
    return <Form onSubmit={handleSubmit}>{
        ({handleSubmit}) => <form onSubmit={handleSubmit}>
            <Box sx={{mt: 5, ml: 5, display: 'flex', maxWidth: 800}}>
                <TextField sx={{mr: 2}} name="login" label="Ім'я"/>
                <TextField sx={{mr: 2}} name="password" type="password" label="Пароль"/>
                <Button sx={{width: 200}} type="submit" variant="contained">Увійти</Button>
            </Box>
        </form>
    }</Form>
}
