import React, {useEffect, useState} from 'react';
import Cookies from "universal-cookie";
import axios from "axios";
import {TableBody, TableCell, TableRow, Table, Button, Box, TableHead} from "@mui/material";

export const Status = ({setLoggedIn}) => {
    const cookies = new Cookies();
    const token = cookies.get('token');
    const [data, setData] = useState();
    const [reload, setReload] = useState(false);
    const [isLoading, setIsLoading] = useState(false);

    const errorHandler = (error) => {
        if (error.response) {
            if(error.response.status === 401 || error.response.status === 403) {
                logOut();
            }
        } else {
            alert("Трапилась якась помилка. Спробуйте оновити сторінку.")
        }
        setIsLoading(false);
    }

    useEffect(() => {
        if (!data || reload) {
            setIsLoading(true);
            axios.get('http://34.194.225.209:8080/air_alarm/status', {
                headers: {
                    Authorization: `Bearer ${token}`
                }
            }).then(function (response) {
                setData(response.data);
                setIsLoading(false);
                if(!response.data.all_messages_processed) {
                    setTimeout(() => setReload(true), 1000)
                }
            }).catch(errorHandler)
            setReload(false);
        }
    }, [token, data, reload])

    const startAlarm = () => {
        setIsLoading(true);
        axios.put(
            "http://34.194.225.209:8080/air_alarm/start",
            {},
            { headers: {Authorization: `Bearer ${token}`}}).then(function (response) {
                setTimeout(() => {
                    setData(null);
                    setIsLoading(false);
                }, 1000)
        }).catch(errorHandler)

    }
    const stopAlarm = () => {
        setIsLoading(true);
        axios.put(
            "http://34.194.225.209:8080/air_alarm/stop",
            {},
            { headers: {Authorization: `Bearer ${token}`}}).then(function (response) {
            setTimeout(() => {
                setData(null);
                setIsLoading(false);
            }, 1000)
        }).catch(errorHandler)
    }

    const logOut = () => {
        cookies.remove('token');
        setLoggedIn(false);
    }

    const getStatus = (status) => {
        switch (status) {
            case 'DELIVERED':
                return 'Доставлено';
            case 'FAILED':
                return 'Помилка';
            case 'WAITING_FOR_DELIVERY':
                return 'Чекае на доставку';

        }
    }

    return data ? <Box>
        <Box sx={{display: 'flex', justifyContent: 'flex-end'}}>
            <Button onClick={logOut}>Вийти</Button>
        </Box>
        <Table sx={{ opacity: isLoading ? 0.5 : 1}}>
            <TableBody>
                <TableRow>
                    <TableCell>Статус</TableCell>
                    <TableCell>{data.status === "STOPPED" ? "Зупинено" : "Розпочато"}</TableCell>
                </TableRow>
                <TableRow>
                    <TableCell>Остання зміна</TableCell>
                    <TableCell>{data.alarm_changed_at}</TableCell>
                </TableRow>
            </TableBody>
        </Table>
        <Table sx={{mt: 4}}>
            <TableHead>
                <TableRow>
                    <TableCell sx={{ fontWeight: 'bold'}}>Назва чату/сторінки</TableCell>
                    <TableCell sx={{ fontWeight: 'bold'}}>Куди відправленно</TableCell>
                    <TableCell sx={{ fontWeight: 'bold'}}>Статус</TableCell>
                    <TableCell sx={{ fontWeight: 'bold'}}>Дата доставки</TableCell>
                </TableRow>
            </TableHead>
            <TableBody>
                {data.message.map(i => (
                    <TableRow key={i.type}>
                        <TableCell>{i.destination}</TableCell>
                        <TableCell>{i.type}</TableCell>
                        <TableCell>{getStatus(i.status)}</TableCell>
                        <TableCell>{i.delivered_at}</TableCell>
                    </TableRow>
                ))}
            </TableBody>
        </Table>
        <Box sx={{mt: 2, ml: 2}}>
            <Button sx={{mr: 2}} color="success" variant="contained" disabled={isLoading} onClick={startAlarm}>Почати</Button>
            <Button color="error" variant="contained" disabled={isLoading} onClick={stopAlarm}>Зупинити</Button>
        </Box>
    </Box> : null;
}
